<template>
    <div class="p-20">
        <div style="margin-bottom: 20px">
            <el-form :model="formData" :rules="rules" ref="formData" style="width: 100%">
                <div style="font-size: 20px;margin-bottom: 40px;background: #eeeeee;padding: 20px">新增操作记录</div>
                <el-form-item label="会员" prop="userInfo">
                    <el-input v-model="formData.userInfo" :disabled="true" placeholder="选择用户" class="input-short"
                              auto-complete="off"></el-input>
                    <el-button type="primary" icon="search" @click.stop="queryAll" size="small"></el-button>
                </el-form-item>
                <el-form-item label="资产" prop="type">
                    <el-select v-model="formData.virtualCoinId" placeholder="请选择">
                        <el-option
                                v-for="item in virtualCoinAndGoods"
                                :key="item.baseSeq"
                                :label="item.chineseName"
                                :value="item.baseSeq">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="类型" prop="type">
                    <el-select v-model="formData.type" placeholder="请选择">
                        <el-option
                                v-for="item in operationTypes"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="金额" prop="amount">
                    <el-input v-model="formData.amount" auto-complete="off" class="input-short" :maxlength="19"></el-input>
                </el-form-item>
                <el-form-item style="margin-left: 9px" label="备注" prop="remark">
                    <el-input type="textarea" v-model="formData.remark" auto-complete="off"
                              class="input-short"></el-input>
                </el-form-item>
                <el-form-item label="是否发送消息">
                    <el-radio-group v-model="formData.sendStatus">
                        <el-radio :label="1">是</el-radio>
                        <el-radio :label="2">否</el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click.stop="submitForm('formData')" v-if="permission.add" size="small">
                        提交
                    </el-button>
                    <el-button @click.stop="goBack" size="small">取消</el-button>
                </el-form-item>
            </el-form>
        </div>

        <el-dialog class="p-20" title="选择用户" :visible.sync="dialogTableVisible">

            <el-input v-model="dialogTableQuery.nameUidNum" class="filter-item" style="width: 300px;margin: 10px"
                      @keyup.native.enter="searchUserInfo"
                      placeholder="请输入用户ID/手机号码"></el-input>
            <el-button class="filter-item" @click.stop="searchUserInfo" size="small">查询</el-button>

            <el-table :data="dialogTableData" highlight-current-row @current-change="handleDialogCurrentChange">
                <el-table-column property="userId" label="UID" width="80"></el-table-column>
                <el-table-column property="name" label="姓名" width="120"></el-table-column>
                <el-table-column property="phoneNum" label="手机号码" width="150"></el-table-column>
                <el-table-column property="accountStatus" label="会员状态" width="120">
                    <template scope="scope">
                        <div v-if="scope.row.accountStatus === 1">正常</div>
                        <div v-else-if="scope.row.accountStatus === 2">禁用</div>
                    </template>
                </el-table-column>
                <el-table-column property="identityValidate" label="实名认证状态" width="200">
                    <template scope="scope">
                        <div v-if="scope.row.identityValidate === 0">未认证</div>
                        <div v-else-if="scope.row.identityValidate === 1">待审核</div>
                        <div v-else-if="scope.row.identityValidate === 3">已认证</div>
                        <div v-else-if="scope.row.identityValidate === 4">驳回</div>
                    </template>
                </el-table-column>
                <el-table-column property="identityType" label="证件类型" width="120"></el-table-column>
                <el-table-column property="idCard" label="证件号码" width="300"></el-table-column>
            </el-table>

            <div class="pagination">
                <el-pagination
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page="dialogTableQuery.page"
                        :page-sizes="[10, 20, 30, 50]"
                        :page-size="dialogTableQuery.pageSize"
                        layout="total, sizes, prev, pager, next, jumper"
                        :total="dialogTableTotalPage">
                </el-pagination>
            </div>

            <div slot="footer" class="dialog-footer">
                <el-button @click.stop="dialogTableVisible = false" size="small">取 消</el-button>
                <el-button type="primary" @click.stop="selectUser" size="small">确 认</el-button>
            </div>
        </el-dialog>

    </div>
</template>

<script>

    import fetch from '@/utils/fetch';
    import {validateEightDecimal} from '@/utils/validate';
    import {virtualCoinAndGoods} from '@/api/virtual';


    export default {
        data() {
            let validateAmounts = (rule, value, callback) => {
                if (!validateEightDecimal(value))
                    callback(new Error('金额输入不合法'));
                callback();
            };
            let validateRemark = (rule, value, callback) => {
                callback();
            };
            return {
                formData: {
                    userId: '',
                    userInfo: '',
                    type: '1',
                    amount: '',
                    remark: '',
                    virtualCoinId: '',
                    sendStatus: 1,
                    category: '2'
                },
                operationTypes: [{value: '1', label: '冻结金额'}, {value: '2', label: '划入金额'}, {value: '3', label: '划出金额'}],
                dialogTableVisible: false,
                dialogTableData: [],
                dialogTableQuery: {
                    nameUidNum: '',
                    pageSize: 10,
                    page: 1
                },
                dialogTableTotalPage: 0,
                dialogTableCurrentRow: null,
                rules: {
                    userInfo: [{required: true, message: '请选择会员'}],
                    type: [{required: true, message: '请选择类型'}],
                    amount: [{required: true, message: '请输入金额'}, {
                        validator: validateAmounts,
                        trigger: 'blur'
                    }],
                    remark: [{validator: validateRemark, trigger: 'blur'}]
                },
                virtualCoinAndGoods: []
            };
        },
        methods: {
            submitForm(formName) {
                let _this = this;
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        _this.confirm();
                    } else {
                        return false;
                    }
                });
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            },

            handleSizeChange(val) {
                this.dialogTableQuery.pageSize = val;
                this.searchUserInfo();
            },
            handleCurrentChange(val) {
                this.dialogTableQuery.page = val;
                this.searchUserInfo();
            },
            resetQuery() {
                this.dialogTableQuery = {
                    nameUidNum: '',
                    pageSize: 10,
                    page: 1
                }
            },
            confirm() {
                let _this = this;
                if(this.formData.amount> 1000000000) {
                    this.$message.error('金额不可以大于1000000000');
                    return false;
                }
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/manualoperation',
                        method: 'post',
                        data: _this.formData
                    }).then(function (response) {
                        _this.getMessage(response.msg, response.result);
                        if (response.result) {
                            _this.resetForm('formData');
                            _this.goBack();
                        }
                    }).catch();
                })
            },
            queryAll() {
                this.resetQuery();
                this.searchUserInfo();
            },
            searchUserInfo() {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/conditionuser',
                        method: 'get',
                        params: _this.dialogTableQuery
                    }).then(function (response) {
                        if (response.result) {
                            _this.dialogTableData = response.data.userList;
                            _this.dialogTableTotalPage = response.data.total;
                        } else {
                            _this.dialogTableData = [];
                        }
                        _this.dialogTableVisible = true;
                    }).catch();
                })
            },
            handleDialogCurrentChange(val) {
                this.dialogTableCurrentRow = val;
            },
            selectUser() {
                if (this.dialogTableCurrentRow === null) {
                    this.getMessage('请选择一个用户带回', false);
                    return;
                }
                this.formData.userId = this.dialogTableCurrentRow.userId;
                this.formData.userInfo = this.dialogTableCurrentRow.userId + ' ' + (this.dialogTableCurrentRow.name ? this.dialogTableCurrentRow.name : '');
                this.dialogTableVisible = false;
            },
            goBack() {
                this.$router.push({path: 'virtualManualOperation'});
            },
            getVirtualCoinAndGoods() {
                let _this = this;
                virtualCoinAndGoods([1, 2], 2).then(response => {
                    if (response.result) {
                        _this.virtualCoinAndGoods = response.data;
                    }
                })
            }
        },
        created() {
            this.getVirtualCoinAndGoods();
        }
    }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
    .input-short {
        width: 300px;
    }
</style>



