export function isvalidUsername(str) {
    const valid_map = ['admin', 'editor']
    return valid_map.indexOf(str.trim()) >= 0
}

/* 合法uri*/
export function validateURL(textval) {
    const urlregex = /^(https?|ftp):\/\/([a-zA-Z0-9.-]+(:[a-zA-Z0-9.&%$-]+)*@)*((25[0-5]|2[0-4][0-9]|1[0-9]{2}|[1-9][0-9]?)(\.(25[0-5]|2[0-4][0-9]|1[0-9]{2}|[1-9]?[0-9])){3}|([a-zA-Z0-9-]+\.)*[a-zA-Z0-9-]+\.(com|edu|gov|int|mil|net|org|biz|arpa|info|name|pro|aero|coop|museum|[a-zA-Z]{2}))(:[0-9]+)*(\/($|[a-zA-Z0-9.,?'\\+&%$#=~_-]+))*$/
    return urlregex.test(textval)
}

/* 小写字母*/
export function validateLowerCase(str) {
    const reg = /^[a-z]+$/
    return reg.test(str)
}

/* 大写字母*/
export function validateUpperCase(str) {
    const reg = /^[A-Z]+$/
    return reg.test(str)
}

/* 大小写字母*/
export function validatAlphabets(str) {
    const reg = /^[A-Za-z]{0,40}$/
    return reg.test(str)
}

/*0-10位中文*/
export function validateChinese(str) {
    const reg = /^[\u4e00-\u9fa5]{0,10}$/;
    return reg.test(str);
}

/*计算字符长度*/
export function characterSize(str) {
    let reg = new RegExp(/[\u4E00-\u9FA5]/, "g");
    let result = str.replace(reg, '00');
    return result.length;
}

/*字母和数字*/
export function validatePassword(str) {
    const reg = /^[A-Za-z0-9]+$/;
    return reg.test(str);
}

/*ip*/
export function validateIP(str) {
    const reg = /^(25[0-5]|2[0-4][0-9]|1[0-9][0-9]|[1-9]?[0-9])\.(25[0-5]|2[0-4][0-9]|1[0-9][0-9]|[1-9]?[0-9])\.(25[0-5]|2[0-4][0-9]|1[0-9][0-9]|[1-9]?[0-9])\.(25[0-5]|2[0-4][0-9]|1[0-9][0-9]|[1-9]?[0-9])$/;
    return reg.test(str);
}

/*ip端口*/
export function validateIpPort(str) {
    const reg = /^([0-9]|[1-9]\d{1,3}|[1-5]\d{4}|6[0-5]{2}[0-3][0-5])$/;
    return reg.test(str);
}

/*小数位数8位*/
export function validateEightDecimal(str) {
    const reg = /^[0-9]+(\.[0-9]{0,8})?$/;
    return reg.test(str);
}

/*小数位数6位*/
export function validateSixDecimal(str) {
    const reg = /^[0-9]+(\.[0-9]{0,6})?$/;
    return reg.test(str);
}

/*小数位数4位*/
export function validateFourDecimal(str) {
    const reg = /^[0-9]+(\.[0-9]{0,4})?$/;
    return reg.test(str);
}

/*小数位数2位*/
export function validateTwoDecimal(str) {
    const reg = /^[0-9]+(\.[0-9]{0,2})?$/;
    return reg.test(str);
}

/*时间段   00:00-00:00*/
export function validateTimeArea(str) {
    const reg = /^(([1][0-9]|[2][0-3]|[0][0-9]|[0-9]):([0-5][0-9])-([1][0-9]|[2][0-3]|[0][0-9]|[0-9]):([0-5][0-9]))$/;
    return reg.test(str);
}

/*两个时间段   00:00-00:00,00:00-00:00*/
export function validateTimeTwoArea(str) {
    const reg = /^(([1][0-9]|[2][0-3]|[0][0-9]|[0-9]):([0-5][0-9])-([1][0-9]|[2][0-3]|[0][0-9]|[0-9]):([0-5][0-9]))$|^(([1][0-9]|[2][0-3]|[0][0-9]|[0-9]):([0-5][0-9])-([1][0-9]|[2][0-3]|[0][0-9]|[0-9]):([0-5][0-9])),(([1][0-9]|[2][0-3]|[0][0-9]|[0-9]):([0-5][0-9])-([1][0-9]|[2][0-3]|[0][0-9]|[0-9]):([0-5][0-9]))$/;
    return reg.test(str);
}

/*时间  0000-00-00 00:00*/
export function validateDateTime(str) {
    const reg = /^((((1[6-9]|[2-9]\d)\d{2})-(0?[13578]|1[02])-(0?[1-9]|[12]\d|3[01]))|(((1[6-9]|[2-9]\d)\d{2})-(0?[13456789]|1[012])-(0?[1-9]|[12]\d|30))|(((1[6-9]|[2-9]\d)\d{2})-0?2-(0?[1-9]|1\d|2[0-8]))|(((1[6-9]|[2-9]\d)(0[48]|[2468][048]|[13579][26])|((16|[2468][048]|[3579][26])00))-0?2-29-))([# ])([1][0-9]|[2][0-3]|[0][0-9]|[0-9]):([0-5][0-9])$/;
    return reg.test(str);
}




